___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --dark-shade-100: #1F2041;
    --dark-shade-75: #1F204175;
    --dark-shade-50: #1F204150;
    --dark-shade-25: #1F204125;
    --dark-shade-5: #1F204105;
    --purple: #BC9CFF;
    --green: #6FCF97;
}
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

*, *::before, *::after{
    box-sizing: inherit;
}

html, body{
    min-height: 100%;
    height: 100%;
    width: 100%;
}

h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 29.26px;
}

h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 19px;
    font-weight: 700;
    line-height: 23.16px;
}

h3{
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    font-weight: 700;
    line-height: 14.63px;
}

a{
    font-family: 'Montserrat', sans-serif;
    color: var(--dark-shade-50);
    font-family: 'Montserrat';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration: none;
}

body p {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
}

li{
    list-style-type: none;
    text-decoration: none;
    margin: 0px;
    padding: 0px;
}
ul{
    list-style-type: none;
    text-decoration: none;
    margin: 0px;
    padding: 0px;
}

button{
    border: 0px;
    background-color: transparent;
}

.wrapper{
    width: 100%;
    height: auto;
    
}

.page-container{
    width: 1160px;
    height: inherit;
    margin-left: auto;
    margin-right: auto;
}




.header_logged-out{
    display: grid;
    grid-template-columns: 391px 440px 329px;
    width: 100%;
    height: 70px;
    background-color:#FFFFFF;
    ;
}

.header_logged-in{
    display: none;
}

.navbar{
    position: relative;
    width: 100%;
}
.list{
    display: flex;
    justify-content: space-between;
}

.list__item{
    margin-top: 26px;
    margin-bottom: 26px;
    flex-shrink: 0;
   /* margin-left: 5px;
    margin-right: 5px;*/
}

.list__icon::after{
    content: "expand_more";
    color: var(--dark-50, rgba(31, 32, 65, 0.50));
    text-align: center;
    font-family: "Material Icons";
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 1px; /* 20.4px */
    position: relative;
    top: 7px;
}

.logo__image{
    margin-top: 15px;
    margin-bottom: 15px;
}
 
.auth{
    display: flex;
}

.auth__button{
    background-color: transparent;
    margin-left: 20px;
}

.auth__button-text{
    margin-top: 27px;
    margin-bottom: 28px;
}

.auth__button-text{
    margin-top: 27px;
    margin-bottom: 28px;
}

.auth__svg{
    z-index: 0;

}

.auth__button-text{
    z-index: 1;
    position: relative;
}
